import * as React from "react"
import { Link } from "gatsby"
import Button from "../Button/Button"
import { FeaturedProductStyles } from "./FeaturesStyles"
import { isNullOrUndefined } from "../../constants/globals"

const FeaturedProduct = ({ feature }) => {
    const { slug, title, picture, introduction } = feature
    if (!isNullOrUndefined(picture)) {
        return (<FeaturedProductStyles>
            <Link to={"artwork/" + slug}>
                <img src={picture.to_featured_path()} alt={title} className="features__item--img" width={467}
                     height={600} />
                {title && true && (<div className="features__item--content">
                    {title && <h4>{title}</h4>}
                    {introduction && <p>{introduction}</p>}
                    <Button text="Read More" as="span" arrow={true} />
                </div>)}
            </Link>
        </FeaturedProductStyles>)
    }
}

export default FeaturedProduct
